"use client";

import Popover from "@/components/ui/popover";
import { useFriends } from "@/hooks/friends-provider";
import { User } from "better-auth";
import { LucideMessageCircle, MessageCircleOff, Trash } from "lucide-react";
import React from "react";
import Avatar from "../layout/avatar";
import { Button } from "../ui/button";
import Chat from "./chat";

export default ({ user }: { user: User }) => {
  const [open, setOpen] = React.useState<Record<string, boolean>>({});
  const { friends, loading, denyRequest } = useFriends();

  const handleOpenChange = (id: string, value: boolean) => {
    setOpen((prev) => ({ ...prev, [id]: value }));
  };

  React.useEffect(() => {
    const initialOpen: Record<string, boolean> = {};
    friends.map((f) => {
      if (!(f.id in open)) initialOpen[f.id] = false;
    });
    if (Object.keys(initialOpen).length > 0)
      setOpen((prev) => ({ ...initialOpen, ...prev }));
  }, [friends]);

  if (loading)
    return (
      <div className="text-muted-foreground text-center">
        Loading friends...
      </div>
    );
  if (friends.length === 0)
    return (
      <div className="text-muted-foreground text-center">
        No friends yet. Add some friends to get started!
      </div>
    );

  return (
    <ul className="flex w-full flex-col gap-6">
      {friends.map((f) => {
        return (
          <Popover key={f.id}>
            <Popover.Trigger asChild>
              <Button variant="default" size={"lg"}>
                <Avatar img={f.image} />
                <span className="ml-2 truncate text-lg font-medium">
                  {f.name}
                </span>
              </Button>
            </Popover.Trigger>
            <Popover.Content
              align="center"
              sideOffset={16}
              side="bottom"
              className="relative flex w-full flex-col items-center justify-start gap-4"
            >
              <div
                className={`flex w-full flex-row flex-wrap items-center justify-start gap-4 ${open[f.id] ? "hidden" : ""}`}
              >
                <Avatar img={f.image} size="md" />
                <div className="w-auto overflow-hidden">
                  <div className="xs:min-w-[60px] min-w-[50px] truncate text-sm font-semibold select-none">
                    Name:
                    <span
                      className={`xs:text-sm xs:w-auto ml-1 w-full truncate text-xs font-medium break-words select-all`}
                    >
                      {f.name}
                    </span>
                  </div>
                  <div className="xs:min-w-[60px] min-w-[50px] text-sm font-semibold select-none">
                    Email:
                    <span
                      className={`xs:text-sm xs:w-auto ml-1 w-full truncate text-xs font-medium break-words select-all`}
                    >
                      {f.email}
                    </span>
                  </div>
                  <div className="xs:min-w-[60px] min-w-[50px] text-sm font-semibold select-none">
                    Bio:
                    <span
                      className={`xs:text-sm xs:w-auto ml-1 w-full text-xs font-medium break-words select-all ${f.bio?.length === 0 ? "text-muted-foreground" : ""}`}
                    >
                      {f.bio?.length === 0 ? "User has no bio" : f.bio}
                    </span>
                  </div>
                </div>
              </div>
              <Chat
                user={user}
                friends={[f]}
                className={open[f.id] ? "" : "hidden"}
              />
              <div className="grid w-full grid-cols-[repeat(auto-fill,minmax(36px,1fr))] gap-6">
                <Button
                  size="icon"
                  className="w-full"
                  onClick={() => handleOpenChange(f.id, !open[f.id])}
                >
                  {open[f.id] ? (
                    <MessageCircleOff />
                  ) : (
                    <LucideMessageCircle />
                  )}{" "}
                </Button>
                <Button
                  size="icon"
                  className="w-full"
                  variant={"destructive"}
                  onClick={() => denyRequest(f.id)}
                >
                  <Trash />
                </Button>
              </div>
            </Popover.Content>
          </Popover>
        );
      })}
    </ul>
  );
};
